.App {
  font-family: 'Ubuntu', sans-serif;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

@media screen and (max-width: 800px) {
  .App {
    font-size: 0.8rem;
  }
}


.header-wrapper {
    width: 90vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 100px;
    margin: 0 auto;
    padding-bottom: 0.5rem;
}

.logo {
    display: flex;
    align-items: center;
}

.logo img {
    width: 100%;
}

.nav {
    display: flex;
    align-items: center;
}

.nav-list {
    list-style: none; 
    display: flex;  
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
}

.nav-link {   
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.5rem;
    text-decoration: none;
    color: #4f4f4f;

}

.nav-link:hover {
   font-weight: bold;
}

.nav-link:active {
    color:#7ccbc8;
}

.cart {
    position: relative;
}

.img-cart {
    height: 50%;
    display: block;
    padding: 25px;
}

.cart:hover {
    cursor: pointer;
}

.cart-counter {
    position: absolute;
    right: 15%;
    top: 15%;
    font-weight: 600;
    font-family: 'Ubuntu', sans-serif;
    background-color: #7ccbc8;
    border-radius: 2rem;
    /* width: 1.5rem;
    height: 1.5rem; */
    font-size: 1.2rem;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 1100px) {
    .header-wrapper {
        height: 80px;
    }
}

@media screen and (max-width: 800px) {
    /* .header-wrapper {
        display: grid;
        grid-template-areas:
        "A B"
        "C C";
        height: auto;
        gap: 10px;
    } */

    .header-wrapper {
        height: 40px;
        padding-top: 0.25rem;
    }

    .logo {
        grid-area: A;
        height: 40px;
        width: 200px;
    }

    .nav {
        height: 40px;
    }

    .nav-link {
        font-size: 1rem;
    }

    .img-cart {
        height: 20px;
        padding: 10px;
    }

    .cart-counter {
        font-size: 0.6rem;
        border-radius: 1rem;
        right: 5%;
        top: 5%;
        width: 1rem;
    }
}

@media screen and (max-width: 500px) {
    .header-wrapper {
        display: grid;
        grid-template-areas:
        "A B"
        "C C";
        height: auto;
        grid-gap: 10px;
        gap: 10px;
    }
    .logo {
        grid-area: A;
    
    }

    .nav {
        grid-area: C;
    }

    .cart {
        grid-area: B;
    }

}
.card {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: auto;
    box-shadow: 0 0 1em rgba(124, 203, 200, .8);
    transition: 0.3s;
} 

.card:hover {
    box-shadow: 0 0 1em rgba(0, 0, 0, .8);
}

.card__photo {
    display: flex;
    box-sizing: content-box;
    position: relative;
    background: black;
}

.card__photo:hover .card__photo_img {
    opacity: 0.4;
    transition: opacity .7s;
}

.card__photo:not(:hover) .card__photo_img {
    opacity: 1;
    transition: opacity .7s;
}

.card__photo:hover .loupe_container,
.card__photo:hover .loupe_img {
    opacity: 1;
    transition: opacity .7s;
    cursor: pointer;
}

.card__photo:not(:hover) .loupe_container,
.card__photo:not(:hover) .loupe_img {
    opacity: 0;
    transition: opacity .4s;
}

.loupe_container {
    position: absolute;
    top: calc(50% - 1rem);
    left: calc(50% - 1rem);
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
    background: #7ccbc8;
    opacity: 0;
}

.loupe_img {
    width: 1rem;
    height: 1rem;
    position: relative;
    top: 25%;
    left: 25%;
    opacity: 0;
}

.card__photo_img {
    width: 100%;
}


.card__description {
    display: grid;
    grid-template-areas: 
    "A A"
    "B B"
    "C D";
    grid-template-columns: 1fr 1fr;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.2rem;
}

.card__description_name {
    grid-area: A;
    text-align: center;
    margin: 5% 0 2% 0; 
}

.card__description_price {
    grid-area: C;
    text-align: start;
    margin: 5% 0 5% 10%;
}

.card__description_more {
    grid-area: B;
    text-align: center;
    -webkit-text-decoration: underline 1px;
            text-decoration: underline 1px;
    font-family: 'Ubuntu', sans-serif;
    font-size: smaller;
    margin: 2%;
}

.card__description_more:hover {
    cursor: pointer;
    font-weight: 700;
}

.inCart {
    grid-area: D;
    margin: 5% 10% 5% 0;
    text-align: end;
}

.add {
    grid-area: D;
    border: none;
    border-radius:35px;
    width: 35px;
    height: 35px;
    align-self: center;
    justify-self: end;
    margin-right: 10%;
    background: none;
    font-family: 'Ubuntu', sans-serif;
    margin: 0 10% 5% 0;
}

.add:hover {
    cursor: pointer;
    background-color: #7ccbc8;
}

.add-img {
    width: 25px;
    float: right;
}

@media screen and (max-width: 1100px) {
    .card {
      width: 45%;
    }
  }

@media screen and (max-width: 900px) {
    .card__description {
        font-size: 1rem;
    }
}
  
@media screen and (max-width: 700px) {
    .add-img {
        width: 20px;
    }
}
  
.container {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .slider {
    position: relative;
    width: 190px;
  }
  
  .slider__track,
  .slider__range,
  .slider__left-value,
  .slider__right-value {
    position: absolute;
  }
  
  .slider__track,
  .slider__range {
    border-radius: 3px;
    height: 5px;
  }
  
  .slider__track {
    background-color: #ced4da;
    width: 100%;
    z-index: 1;
  }
  
  .slider__range {
    background-color: #9fe5e1;
    z-index: 2;
  }
  
  .slider__left-value,
  .slider__right-value {
    color: #4f4f4f;
    font-size: 15px;
    margin-top: 20px;
  }
  
  .slider__left-value {
    left: 4px;
  }
  
  .slider__right-value {
    right: -2px;
  }
  
  /* Removing the default appearance */
  .thumb,
  .thumb::-webkit-slider-thumb {
   /* -webkit-appearance: none; */
    -webkit-tap-highlight-color: transparent;
  } 
 
  .thumb {
    pointer-events: none;
    position: absolute;
    height: 0;
    width: 190px;
    outline: none;
    -webkit-appearance: none;
            appearance: none;
    z-index: 5;
    accent-color: #7ccbc8;
  }
  
  .thumb--left {
    z-index: 3;
  }
  
  .thumb--right {
    z-index: 4;
  }
  
  /* For Chrome browsers */
  .thumb::-webkit-slider-thumb {
    background-color: #f1f5f7;
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px #ced4da;
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
  }
  
  /* For Firefox browsers */
  .thumb::-moz-range-thumb {
    background-color: #f1f5f7;
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px #ced4da;
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
  }
  
