    color: #4f4f4f;
}

.cart_item_delete img{
    width: 20px;
}

.cart_item_delete img:hover {
    cursor: pointer;
}

.amount_buttons_container_cart {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1rem;
    justify-content: space-between;
}


.amount_button_cart {
    background: transparent;
    border-color: transparent;
    color: #4f4f4f;
    cursor: pointer;
    padding: 1rem 0px;
    font-weight: bold;
    width: 2.5rem;
    /* height: 2rem; */
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 900px) {
    
    .cart_header {
        font-size: 1.5rem;
    }

    .cart_item {
        font-size: 1rem;
    }

    .cart_item_name {
        font-size: 0.8rem;
    }

    .amount_buttons_container_cart {
        margin-bottom: 0;
    }
}

@media screen and (max-width: 500px) {
    .cart_item_price, .cart_header_price {
        display: none;
    }

    .cart_item, .cart_header {
        display: grid;
        grid-template-columns: 25% 25% 25% 25%;
    }

    .cart_header {
        font-size: 1rem;
    }

    .cart_item_img img {
        width: 50px;
    }

    .amount_button_cart {
        width: 1.5rem;
        font-size: 0.8rem;
    }

    .cart_item_name {
        font-size: 0.6rem;
    }

    .cart_item {
        font-size: 0.8rem;
    }

    .cart_item_delete img {
        width: 15px;
    }

    .total h3 {
        font-size: 0.8rem;
    }

    .total h1 {
        font-size: 1rem;
    }
}


.footer-wrapper {
    background-color: #7ccbc8;
    color: #4f4f4f;
    padding: 0.8rem 5vw;
    font-size: 1rem;
    display: flex;
    flex-direction: row;
    margin-top: 1rem;
}

.footer-text {
    margin: auto;
}
